@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
*{margin:0}.header{font-family:'Satisfy', cursive;display:flex;position:fixed;background-color:rgba(0,0,0,0.3);height:150px;width:100vw;justify-content:center;border-bottom:5px solid #000}.header h1{margin-top:35px;font-size:8em;text-shadow:3px 3px #fff}.form{margin-top:0.5%;position:fixed;display:flex;justify-content:center;width:100vw}.form input{font-family:Arial, Helvetica, sans-serif;border:2px solid #000;background-color:#b6cece;margin-left:50px;border-radius:3px;height:25px;width:700px;font-size:17px}.form input:hover{background-color:#fff}.form input:focus{background-color:#fff}.form input::-webkit-input-placeholder{color:#000;font-size:15px;font-family:'Satisfy', cursive}.form input:-ms-input-placeholder{color:#000;font-size:15px;font-family:'Satisfy', cursive}.form input::-ms-input-placeholder{color:#000;font-size:15px;font-family:'Satisfy', cursive}.form input::placeholder{color:#000;font-size:15px;font-family:'Satisfy', cursive}.form button{border:2px solid #000;background-color:rgba(255,255,255,0.5);font-size:10px;width:80px;height:30px;border-radius:3px;margin-left:5px;font-size:0.8em}.form button:hover{background-color:rgba(184,22,22,0.9);color:white}.headerSpace{height:220px;background-image:linear-gradient(#a7fd96, #ff9650)}.App{background-image:linear-gradient(#ff9650, #a7fd96, #2cb3b3, #a33d9e);height:150%;display:flex;flex-wrap:wrap;justify-content:space-evenly}.wrap{display:flex;flex-flow:wrap;height:200px;width:350px;color:#3d0a3d;margin:50px 0;background-color:rgba(194,253,209,0.5);border:4px solid #000;border-radius:10px;padding:5px}.wrap .title{font-size:2.2em;text-align:left;font-family:Arial, Helvetica, sans-serif;width:70%;height:80%;margin-top:-5%;margin-left:25%;text-overflow:ellipsis;overflow:scroll}.wrap ul{text-decoration:none;font-family:Arial, Helvetica, sans-serif;text-overflow:ellipsis;overflow:scroll;height:300px;width:100%;text-align:justify;background-color:rgba(218,255,233,0.1);border-radius:10px}.wrap ul:hover{background-color:#daffe9}.wrap img{margin-left:-45px;margin-top:-90px;border:5px solid #000;border-radius:50%;max-width:300px;max-height:150px}.wrap::after{visibility:hidden}footer{font-family:'Satisfy', cursive;width:100vw;height:10%;display:flex;justify-content:space-evenly;background-color:#000;color:#aaa}footer a{color:#aaa;padding-top:3px;text-align:left;width:200px;font-size:x-large;text-decoration:none}footer small{padding-top:3px;font-family:'Satisfy', cursive;font-size:x-large}

